
import { defineComponent, onMounted, ref, computed } from 'vue'
import PageSearch from '@/components/page-search'
import { useRouter } from 'vue-router'
import { searchFormConfig } from '../main/config/search.config'
import { useI18n } from 'vue-i18n'
import { PageInfo } from '@/views/main/customer/edit/types'
import PageContent from '@/components/page-content'
import { qualityStatusList } from '@/constant/option'
import ImportDialog from '@/base-ui/importdialog'
import {
  ElMessage,
  ElMessageBox,
  ElCollapse,
  ElCollapseItem
} from 'element-plus'

export default defineComponent({
  name: 'kitmanagement-main',
  components: {
    PageSearch,
    PageContent,
    ImportDialog,
    ElCollapse,
    ElCollapseItem
  },
  setup() {
    const { t } = useI18n()
    const router = useRouter()
    const activeNames = ref(['1'])
    const rowDataDetail = ref<any>({})
    const handleChange = (val: string[]) => {
      console.log(val)
    }
    const importConfig = ref({
      tip: t('jpg/png files with a size less than 500kb'),
      href: '/static/customerTm.xlsx'
    })
    const keyWords = ref({})
    let importDialogVisible = ref(false)
    let cellDialogVisible = ref(false)
    const handleQueryClick = (val: any) => {
      keyWords.value = val
      pageInfo.value.currentPage = 1
      searchInfo()
    }
    const handleSuccess = (file: any) => {
      console.log(file, '父filesuccess')
    }
    const handleAddClick = (val: any) => {
      router.push({ path: '/main/Inventory/kitmanagement/newKit' })
    }
    const qualityStatusArray = computed(() => qualityStatusList)
    const handleRow = (rowData: any, label: any) => {
      if (label === 'Edit') {
        router.push({
          path: '/main/Inventory/kitmanagement/newKit',
          query: rowData
        })
      }
      if (label === 'Delete') {
        ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
          distinguishCancelAndClose: true,
          confirmButtonText: t('confirm'),
          cancelButtonText: t('cancel'),
          customClass: 'message-box',
          center: true
        })
          .then(() => {
            ElMessage({
              type: 'success',
              message: t('deleteSuccessful')
            })
          })
          .catch((action: any) => {
            ElMessage({
              type: 'info',
              message: action === 'cancel' ? t('deleteFailed') : t('canceled')
            })
          })
      }
      console.log(rowData, label, 'row父')
    }
    const handleCell = (rowData: any) => {
      rowDataDetail.value = rowData
      cellDialogVisible.value = true

      console.log(rowData, '主')
    }
    const contentTableConfig = {
      propList: [
        {
          prop: 'barcode',
          label: t('barcode')
        },
        {
          prop: 'sku',
          label: t('sku'),
          handleCell: handleCell
        },
        {
          prop: 'kitDescr',
          label: t('kitDescr')
        },
        {
          prop: 'note',
          label: t('note')
        }
      ],
      showIndexColumn: false,
      showSelectColumn: false,
      isOperation: true,
      operation: {
        label: t('operation'),
        width: '300',
        cols: [
          {
            label: t('edit'),
            type: 'primary',
            handleRow: handleRow
          },
          {
            label: t('delete'),
            type: 'danger',
            handleRow: handleRow
          }
        ]
      },
      childrenProps: {
        rowKey: 'id',
        treeProp: {
          children: 'children'
        }
      },
      border: false,
      showFooter: true,
      totalCount: 12
    }
    const handleBtn = (label: any) => {
      if (label === t('import')) {
        importDialogVisible.value = true
        console.log(importDialogVisible.value, 'importDialogVisible1')
      }
      console.log(label, '1111')
    }
    const searchInfo = () => {
      const params = Object.assign({}, keyWords.value, {
        pageSize: pageInfo.value.pageSize,
        currentPage: pageInfo.value.currentPage
      })
      console.log(params, 'params查询')
    }
    const btnConfig = {
      isActions: true,
      addBtn: true,
      actions: [
        {
          label: t('import'),
          type: 'primary',
          style: 'plain',
          handleBtn: handleBtn
        },
        {
          label: t('export'),
          type: 'primary',
          style: 'plain',
          handleBtn: handleBtn
        }
      ]
    }
    const handleSizeChange = (val: number) => {
      pageInfo.value.pageSize = val
      searchInfo()
    }
    const handleCurrentChange = (val: number) => {
      pageInfo.value.currentPage = val
      searchInfo()
    }
    const beforeUpload = (file: any) => {
      if (file.type !== 'image/jpeg') {
        ElMessage.error('Avatar picture must be JPG format!')
        return false
      } else if (file.size / 1024 / 1024 > 2) {
        ElMessage.error('Avatar picture size can not exceed 2MB!')
        return false
      }
      return true
    }
    const pageInfo = ref<PageInfo>({
      totalCount: 100,
      pageSizes: [10, 20, 50, 100],
      handleSizeChange: handleSizeChange,
      handleCurrentChange: handleCurrentChange,
      currentPage: 1,
      pageSize: 20
    })
    const tableData = ref([
      {
        barcode: '23313444',
        sku: '2BE-HSR1000',
        kitDescr: '6 pack',
        note: 'aaa'
      },
      {
        barcode: '23313445',
        sku: '2BE-HS120S',
        kitDescr: '35 pack'
      }
    ])
    onMounted(() => {
      searchInfo()
    })

    return {
      searchFormConfig,
      handleQueryClick,
      contentTableConfig,
      pageInfo,
      tableData,
      handleCurrentChange,
      handleSizeChange,
      qualityStatusArray,
      btnConfig,
      ElMessage,
      importConfig,
      importDialogVisible,
      handleSuccess,
      beforeUpload,
      searchInfo,
      keyWords,
      handleAddClick,
      handleCell,
      cellDialogVisible,
      activeNames,
      handleChange,
      rowDataDetail
    }
  }
})
